.description {
    padding: 55px 0; 
    &__container {
        display: flex;
        justify-content: center;
    }
    &__content {
        max-width: 952px;
        width: 100%;
    }

    &__descr {
        &:not(:last-child) {
            margin-bottom: 70px;
        }
    }

    &__item {
        display: flex;
        align-items: center;
        flex-direction: column;
        &:not(:last-child) {
            margin-bottom: 20px;
        }
        &-min {
            max-width: 779px;
        }
    }
}

@media screen and (max-width: 991px) {
    .description {
        &__descr {
            &:not(:last-child) {
                margin-bottom: 40px;
            }
        }

        &__item {
            &:not(:last-child) {
                margin-bottom: 40px;
            }
        }
    }
}

@media screen and (max-width: 639px) {
    .description {
        padding: 40px 0;
        
        &__descr {
            &:not(:last-child) {
                margin-bottom: 30px;
            }
        }

        &__item {
            &:not(:last-child) {
                margin-bottom: 15px;
            }
        }
    }
}